<template>
  <!-- 跟进管理 -->
  <div class="follow_box">
    <div class="follow_item">
      <el-tabs class="eltabs" v-model="activeName"  @tab-click="handleClick" >
        <el-tab-pane label="跟进计划" name="first" class="plan">
            <followplan></followplan>
        </el-tab-pane>
        <el-tab-pane label="跟进记录" name="second" class="plan">
            <follownote></follownote>
        </el-tab-pane>
      </el-tabs>
      <el-button v-if="isouter==1" @click="returnrouter" class="btnposi" size="mini">
          返回
      </el-button>
    </div>
  </div>
</template>

<script>
import { getCurrentInstance, onMounted, reactive, toRefs } from "vue";

import followplan from "../followup_management/followplan.vue"
import follownote from "../followup_management/follownote.vue"
export default {
    components: {
        followplan,
        follownote
  },
  setup() {
    const { proxy } = getCurrentInstance()
    const state = reactive({
       activeName:'first',
       isouter:null
    });
    const handleClick=(tab, event)=>{
         console.log(tab, event)
    }
    const returnrouter = () => {
      proxy.$router.go(-1)
    }
    onMounted(()=>{
      if(proxy.$route.query.str&&proxy.$route.query.str!=''){
        proxy.activeName = proxy.$route.query.str
      }else{
        proxy.activeName = 'first'
      }
      proxy.isouter = proxy.$route.query.isouter
    })
    return {
      ...toRefs(state),
      handleClick,
      returnrouter
    };
  },
};
</script>

<style  scoped>
.follow_box {
  height: 100%;
  background-color: #fff;
 
}
.follow_item {
  background: #fff;
  position: relative;
}
.btnposi{
  position: absolute;
  top: 10px;
  right: 15px;
}
.el-tab-pane.plan{
  padding-top: 0;
}
</style>
<style>
.el-tab-pane.plan{
  padding: 10px;
  box-sizing: border-box;
  padding-bottom: 0;
}
.eltabs.el-tabs .el-tabs__nav-wrap{
  padding: 0 10px;
  box-sizing: border-box;
}
.eltabs.el-tabs .el-tabs__header{
  margin: 0;
}
</style>