  <div>
    <div class="beside">
      <div class="follow_con_box">
        <div>
          <el-input
            style="width: 300px"
            size="mini"
            v-model="keyword"
            placeholder="编号/主题"
          ></el-input>
          <el-select
            class="top_sel"
            size="mini"
            v-model="followId"
            placeholder="客户"
          >
            <el-option
              v-for="item in followlist"
              :key="item.value1"
              :label="item.label"
              :value="item.value1"
            >
            </el-option>
          </el-select>
          <el-select
            class="top_sel"
            size="mini"
            v-model="follow_statusId"
            placeholder="形式"
          >
            <el-option
              v-for="item in follow_status_list"
              :key="item.value2"
              :label="item.label"
              :value="item.value2"
            >
            </el-option>
          </el-select>
          <el-select
            class="top_sel"
            size="mini"
            v-model="follow_statusId"
            placeholder="跟进人"
          >
            <el-option
              v-for="item in follow_status_list"
              :key="item.value2"
              :label="item.label"
              :value="item.value2"
            >
            </el-option>
          </el-select>
          <el-button
            class="search_btn"
            type="primary"
            size="mini"
            style="margin-left: 10px"
            @click="search()"
            >查 询</el-button
          >
          <el-button size="mini" @click="resetForm()">重 置</el-button>
        </div>
      </div>
      <div class="follow_new">
        <el-button type="primary" size="mini" @click="onSubmit()"
          >新 建</el-button
        >
      </div>
    </div>
    <div class="follow_line2">
      <div>
        <el-date-picker
          v-model="followup_date"
