          style="width: 300px"
          type="daterange"
          range-separator="至"
          start-placeholder="跟进日期"
          end-placeholder="跟进日期"
        >
        </el-date-picker>
      </div>
    </div>
    <div class="follow_table">
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        :height="$store.state.innerheight - 240"
        :header-cell-style="{background:'#f5f5f5'}"
        size="mini"
      >
        <el-table-column prop="follow_number" label="跟进记录编号" width="100">
        </el-table-column>
        <el-table-column prop="follow_topics" label="跟进记录主题" width="">
        </el-table-column>
        <el-table-column prop="customer" label="客户" width="">
        </el-table-column>
        <el-table-column prop="away" label="形式" width=""> </el-table-column>
        <el-table-column prop="planman" label="跟进人" width="">
        </el-table-column>
        <el-table-column prop="plan_time" label="跟进时间" width="">
        </el-table-column>
        <el-table-column prop="goal" label="目的" width=""> </el-table-column>
        <el-table-column prop="cus_metting" label="客户异议" width="">
        </el-table-column>
        <el-table-column prop="handle" label="处理意见" width="">
        </el-table-column>
        <el-table-column prop="promise" label="获得承诺" width="">
        </el-table-column>
        <el-table-column prop="promise" label="业务类型" width="">
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template #default="scope">
            <el-button @click="getEdit(scope.row)" type="text" size="mini"
              >编辑</el-button
            >
            <el-button
              type="text"
              style="color: red"
              size="mini"
              @click="handleDel(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
