</template>

<script>
import { reactive, toRefs } from "vue-demi";
import { getCurrentInstance } from "@vue/runtime-core";
import follownotede from "../followup_management/follownote_detail.vue";
export default {
  components: {
    follownotede,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      currentPage4:1,
      keyword: "",
      followId: "",
      followlist: [],
      follow_statusId: "",
      follow_status_list: [],
      overdue_follow: false,
      followup_date: "",
      planId: "",
      planman: [],
      awayId: "",
      followaway: [],
      isshow: false,
      tableData: [
        {
          follow_number: "1",
          follow_topics: "1",
          customer: "1",
          away: "1",
          planman: "1",
          plan_time: "1",
          goal: "",
          cus_metting: "1",
          handle: "1",
          promise: "1",
        },
        {
          follow_number: "1",
          follow_topics: "1",
          customer: "1",
          away: "1",
          planman: "1",
          plan_time: "1",
          goal: "",
          cus_metting: "1",
          handle: "1",
          promise: "1",
        },
        {
          follow_number: "1",
          follow_topics: "1",
          customer: "1",
          away: "1",
          planman: "1",
          plan_time: "1",
          goal: "",
          cus_metting: "1",
          handle: "1",
          promise: "1",
        },
      ],
    });
    const onSubmit = () => {
      // proxy.$refs.follownotede.newshow();
      proxy.$router.push({
        path:'/follow_record',
        query:{
          obj:{},
          isedit:0,
          isouter:1
        }
      })
    };
    const getEdit = (res) => {
      console.log(res)
      proxy.$router.push({
        path:'/follow_record',
        query:{
          obj:res,
          isedit:1,
          isouter:1
        }
      })
    };
    const handleSizeChange = (val) => {
        console.log(`${val} items per page`)
    }
    const handleCurrentChange = (val) => {
        console.log(`current page: ${val}`)
    }
    return {
      ...toRefs(state),
      onSubmit,
      getEdit,
      handleSizeChange,
      handleCurrentChange
    };
  },
};
</script>

<style  scoped>
.beside {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}

.follow_look {
  padding-left: 165px;
  box-sizing: border-box;
}
.top_sel {
  margin-left: 10px;
}
